"use client"

import { useState, useCallback } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Button } from "@/components/ui/button"
import { Alert, AlertDescription } from "@/components/ui/alert"
import QRScanner from "@/components/QRScanner"
import QRGenerator from "@/components/QRGenerator"

export default function Home() {
  const [activeTab, setActiveTab] = useState("qr-entry")
  const [scanStatus, setScanStatus] = useState<{
    success?: string;
    error?: string;
  }>({})

  const handleScan = useCallback(async (data: string) => {
    try {
      const scanData = JSON.parse(data)
      const response = await fetch('/api/qr/entry', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          userId: scanData.userId,
          latitude: scanData.location?.latitude,
          longitude: scanData.location?.longitude,
        }),
      })

      if (!response.ok) {
        throw new Error('Failed to record attendance')
      }

      setScanStatus({
        success: 'Attendance recorded successfully!',
        error: undefined,
      })
    } catch (error) {
      setScanStatus({
        success: undefined,
        error: 'Failed to record attendance. Please try again.',
      })
    }
  }, [])

  const handleError = useCallback((error: string) => {
    setScanStatus({
      success: undefined,
      error,
    })
  }, [])

  const handleQRGenerate = useCallback((qrData: string) => {
    console.log('QR Code generated:', qrData)
  }, [])

  return (
    <main className="min-h-screen bg-gray-100 p-8">
      <div className="max-w-7xl mx-auto">
        <h1 className="text-4xl font-bold text-gray-900 mb-8">
          Attendance Management System
        </h1>

        <Tabs defaultValue="qr-entry" className="space-y-4">
          <TabsList className="grid w-full grid-cols-4">
            <TabsTrigger value="qr-entry">QR Entry</TabsTrigger>
            <TabsTrigger value="qr-generate">Generate QR</TabsTrigger>
            <TabsTrigger value="dashboard">Dashboard</TabsTrigger>
            <TabsTrigger value="reports">Reports</TabsTrigger>
          </TabsList>

          <TabsContent value="qr-entry" className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle>QR Code Entry</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {scanStatus.success && (
                    <Alert className="bg-green-50 border-green-200">
                      <AlertDescription>{scanStatus.success}</AlertDescription>
                    </Alert>
                  )}
                  {scanStatus.error && (
                    <Alert variant="destructive">
                      <AlertDescription>{scanStatus.error}</AlertDescription>
                    </Alert>
                  )}
                  <QRScanner onScan={handleScan} onError={handleError} />
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="qr-generate" className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle>Generate QR Code</CardTitle>
              </CardHeader>
              <CardContent>
                <QRGenerator onGenerate={handleQRGenerate} />
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="dashboard" className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle>Today's Attendance</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                  <Card>
                    <CardHeader>
                      <CardTitle className="text-lg">Total Check-ins</CardTitle>
                    </CardHeader>
                    <CardContent>
                      <p className="text-3xl font-bold">0</p>
                    </CardContent>
                  </Card>
                  <Card>
                    <CardHeader>
                      <CardTitle className="text-lg">Present Today</CardTitle>
                    </CardHeader>
                    <CardContent>
                      <p className="text-3xl font-bold">0</p>
                    </CardContent>
                  </Card>
                  <Card>
                    <CardHeader>
                      <CardTitle className="text-lg">Remote Check-ins</CardTitle>
                    </CardHeader>
                    <CardContent>
                      <p className="text-3xl font-bold">0</p>
                    </CardContent>
                  </Card>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="reports" className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle>Attendance Reports</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <Button variant="outline">Daily Report</Button>
                    <Button variant="outline">Weekly Report</Button>
                    <Button variant="outline">Monthly Report</Button>
                  </div>
                  <Card className="mt-4">
                    <CardContent className="pt-6">
                      <p className="text-gray-500 text-center">Select a report type to view data</p>
                    </CardContent>
                  </Card>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </main>
  )
}
