"use client"

import { useState } from 'react'
import QRCode from 'qrcode'
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Alert, AlertDescription } from "@/components/ui/alert"

interface QRGeneratorProps {
  onGenerate?: (qrData: string) => void
}

export default function QRGenerator({ onGenerate }: QRGeneratorProps) {
  const [userId, setUserId] = useState('')
  const [qrUrl, setQrUrl] = useState('')
  const [error, setError] = useState('')
  const [generating, setGenerating] = useState(false)

  const generateQR = async () => {
    if (!userId) {
      setError('Please enter a User ID')
      return
    }

    setGenerating(true)
    try {
      const qrData = {
        userId,
        timestamp: new Date().toISOString(),
        type: 'attendance'
      }

      const qrCodeData = JSON.stringify(qrData)
      const url = await QRCode.toDataURL(qrCodeData, {
        width: 800, // Increased size for better quality
        margin: 2,
        color: {
          dark: '#000000',
          light: '#ffffff'
        },
        errorCorrectionLevel: 'H' // Highest error correction level
      })
      setQrUrl(url)
      setError('')
      onGenerate?.(qrCodeData)
    } catch (err) {
      console.error('Error generating QR code:', err)
      setError('Failed to generate QR code')
    } finally {
      setGenerating(false)
    }
  }

  const downloadQR = () => {
    if (!qrUrl || !userId) return

    // Create a temporary link element
    const link = document.createElement('a')
    link.href = qrUrl
    link.download = `attendance-qr-${userId}.png`
    
    // Append to body, click, and remove
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
  }

  return (
    <Card className="w-full max-w-xl mx-auto">
      <CardHeader>
        <CardTitle className="text-center">Generate QR Code</CardTitle>
      </CardHeader>
      <CardContent className="space-y-6">
        {error && (
          <Alert variant="destructive">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        <div className="space-y-4">
          <div className="space-y-2">
            <label htmlFor="userId" className="text-sm font-medium text-gray-700">
              User ID
            </label>
            <Input
              id="userId"
              type="text"
              placeholder="Enter User ID"
              value={userId}
              onChange={(e) => setUserId(e.target.value)}
              className="w-full text-lg h-12"
            />
          </div>
          
          <Button 
            onClick={generateQR}
            className="w-full h-12 text-lg font-medium"
            disabled={!userId || generating}
          >
            {generating ? 'Generating...' : 'Generate QR Code'}
          </Button>
        </div>

        {qrUrl && (
          <div className="flex flex-col items-center space-y-6 pt-4">
            <div className="relative p-6 bg-white rounded-lg shadow-sm border">
              <img
                src={qrUrl}
                alt="QR Code"
                className="w-64 h-64"
              />
            </div>
            <div className="space-y-4 w-full">
              <Button 
                onClick={downloadQR}
                variant="outline"
                className="w-full h-12 text-lg font-medium"
              >
                Download QR Code
              </Button>
              <p className="text-sm text-center text-gray-500 max-w-sm mx-auto">
                Click the button above to download the QR code image, then scan it with the QR Entry scanner
              </p>
            </div>
          </div>
        )}
      </CardContent>
    </Card>
  )
}
