"use client"

import { useEffect, useRef, useState } from "react"
import { Html5Qrcode } from "html5-qrcode"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Input } from "@/components/ui/input"

interface QRScannerProps {
  onScan: (data: string) => void
  onError: (error: string) => void
}

export default function QRScanner({ onScan, onError }: QRScannerProps) {
  const [error, setError] = useState<string>("")
  const [scanning, setScanning] = useState(false)
  const [lastScanned, setLastScanned] = useState<string>("")
  const [mounted, setMounted] = useState(false)
  const html5QrCode = useRef<Html5Qrcode | null>(null)
  const fileInputRef = useRef<HTMLInputElement>(null)

  useEffect(() => {
    setMounted(true)
    return () => {
      stopScanner()
    }
  }, [])

  const startScanner = async () => {
    try {
      if (!html5QrCode.current) {
        html5QrCode.current = new Html5Qrcode("qr-reader")
      }

      console.log("Starting QR scanner...")

      await html5QrCode.current.start(
        { facingMode: "environment" },
        {
          fps: 10,
          qrbox: { width: 250, height: 250 },
          aspectRatio: 1.0,
        },
        async (decodedText) => {
          console.log("Raw QR scan result:", decodedText)
          handleScanResult(decodedText)
        },
        (errorMessage) => {
          console.debug('Scanning attempt:', errorMessage)
        }
      )

      setScanning(true)
      setError("")
      console.log("Scanner started successfully")
    } catch (err) {
      console.error('Failed to start scanner:', err)
      setError(
        "Failed to start camera. Please ensure:\n" +
        "1. You've granted camera permissions\n" +
        "2. You're using a secure connection (HTTPS)\n" +
        "3. Your device has a working camera"
      )
      onError("Camera access failed")
    }
  }

  const stopScanner = async () => {
    try {
      if (html5QrCode.current && scanning) {
        await html5QrCode.current.stop()
        html5QrCode.current = null
      }
    } catch (err) {
      console.error('Failed to stop scanner:', err)
    }
    setScanning(false)
    setLastScanned("")
  }

  const handleScanResult = async (decodedText: string) => {
    try {
      // Prevent duplicate scans
      if (decodedText === lastScanned) {
        return
      }
      setLastScanned(decodedText)

      const qrData = JSON.parse(decodedText)
      console.log("Parsed QR data:", qrData)
      
      await stopScanner()
      onScan(JSON.stringify(qrData))
    } catch (err) {
      console.error('Invalid QR code data:', err)
      setError("Invalid QR code format. Please use a valid attendance QR code.")
    }
  }

  const handleFileUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (!file) return

    try {
      if (!html5QrCode.current) {
        html5QrCode.current = new Html5Qrcode("qr-reader")
      }

      console.log("Scanning uploaded file...")
      const result = await html5QrCode.current.scanFile(file, true)
      console.log("File scan result:", result)
      handleScanResult(result)
    } catch (err) {
      console.error('Failed to scan file:', err)
      setError("Could not read QR code from image. Please try a different image or use the camera.")
    }
  }

  const triggerFileUpload = () => {
    fileInputRef.current?.click()
  }

  if (!mounted) {
    return null
  }

  return (
    <Card className="w-full max-w-xl mx-auto">
      <CardHeader>
        <CardTitle className="text-center">Scan QR Code</CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        {error && (
          <Alert variant="destructive">
            <AlertDescription className="whitespace-pre-line">
              {error}
            </AlertDescription>
          </Alert>
        )}

        <div className="relative aspect-square max-h-[500px] bg-black rounded-lg overflow-hidden">
          <div 
            id="qr-reader" 
            className="w-full h-full"
            style={{ position: 'relative' }}
          />
          {!scanning && (
            <div className="absolute inset-0 flex flex-col items-center justify-center bg-gray-900 text-white">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
                className="mb-2"
              >
                <path d="M23 7v4h-4" />
                <path d="M1 17v-4h4" />
                <path d="M4 8V5a1 1 0 0 1 1-1h4" />
                <path d="M4 16v3a1 1 0 0 0 1 1h4" />
                <path d="M20 16v3a1 1 0 0 1-1 1h-4" />
                <path d="M20 8V5a1 1 0 0 0-1-1h-4" />
              </svg>
              <p className="text-sm">Camera not active</p>
            </div>
          )}
        </div>

        <div className="flex flex-col gap-4">
          <Button 
            onClick={scanning ? stopScanner : startScanner}
            className="w-full"
            size="lg"
            variant={scanning ? "outline" : "default"}
          >
            {scanning ? "Stop Scanner" : "Start Scanner"}
          </Button>

          <div className="relative">
            <Input
              ref={fileInputRef}
              type="file"
              accept="image/*"
              className="hidden"
              onChange={handleFileUpload}
            />
            <Button 
              onClick={triggerFileUpload}
              variant="outline"
              className="w-full"
              size="lg"
            >
              Upload QR Code Image
            </Button>
          </div>
        </div>

        <div className="text-center space-y-2">
          <p className="text-sm text-gray-500">
            {scanning 
              ? "Point your camera at a QR code to scan" 
              : "Use the camera or upload a QR code image"}
          </p>
          <p className="text-xs text-gray-400">
            Make sure the QR code is well-lit and clearly visible
          </p>
        </div>
      </CardContent>
    </Card>
  )
}
